import React, { useState } from "react";
import ReactDOM from "react-dom";
import { CssBaseline, Typography } from "@material-ui/core";
import { DropzoneArea } from "material-ui-dropzone";
import { getLigaturesFromBuffer } from "../dist";

const App = () => {
  const [fontBase64, setFontBase64] = useState(null);
  const [fontBuffer, setFontBuffer] = useState(null);

  const ligatures = getLigaturesFromBuffer(fontBuffer);
  debugger;
  return (
    <div style={{ maxWidth: "1000px", margin: "0 auto" }}>
      <CssBaseline />
      <Typography variant="h3" component="h1" gutterBottom>
        Ligatures
      </Typography>
      <DropzoneArea
        acceptedFiles={[".woff2"]}
        filesLimit={1}
        onChange={([file]) => {
          if (!file) {
            return;
          }
          const readerBase64 = new FileReader();
          readerBase64.readAsDataURL(file);
          readerBase64.onload = () => setFontBase64(readerBase64.result);

          const readerArrayBuffer = new FileReader();
          readerArrayBuffer.readAsArrayBuffer(file);
          readerArrayBuffer.onload = () =>
            setFontBuffer(readerArrayBuffer.result);
        }}
        dropzoneText={
          <span>
            <Typography>Drop your font files</Typography>
            <Typography variant="caption">Files are not uploaded</Typography>
          </span>
        }
        showPreviewsInDropzone={false}
      />
      {fontBase64}
    </div>
  );
};

ReactDOM.render(<App />, document.getElementById("app"));

console.log("Hello");
